@import url('https://fonts.googleapis.com/css2?family=Salsa&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {

  input[type="number"]::-webkit-inner-spin-button,
  input[type="number"]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
}

*{
  font-family: 'Salsa', cursive;
}